export enum FileTypeEnum {
  IMAGE = "image",
  PDF = "pdf",
  DOCX = "docx",
  EXCEL = "excel",
  CSV = "csv",
  AUDIO = "audio",
  VIDEO = "video",
  TXT = "txt",
  PPT = "ppt",
  ZIP = "zip",
  CODE = "code",
  DEFAULT_UNKNOWN = "default_unknown",
}

export type ChooseFileInfo =
  | {
      from: "Taro_Image_Chooser"; // taro
      type: FileTypeEnum; // 
      tempFilePath: string; // Taro
      size: number; // 
      file: {
        filePath: string;
      }; // Taro
    }
  | {
      from: "H5_Input_Chooser"; // H5
      type: FileTypeEnum; // 
      tempFilePath: string; // Taro
      size: number; // 
      file: File;
    };
